@use '../assets/mixins' as *;
@use '../assets/vars' as *;
.why-us {
  position: relative;
  background: linear-gradient(to right, #f5f7f9 50%, #fff 50%);
  &::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(245, 247, 249, 1) 0%, rgba(255, 255, 255, 0) 100%);
  }
  &-inner {
    position: relative;
    z-index: 3;
    padding: var(--p100) var(--p260);
    display: flex;
    flex-direction: column;
    gap: vw(85, 1920);
  }

  &-header-wrap {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
  }

  &-header {
    width: vw(655, 1920);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: vw(20, 1920);
  }

  &-get-started {
    width: vw(598, 1920);
    display: flex;
    gap: vw(40, 1920);
    align-items: center;
    justify-content: space-between;
    padding: vw(34, 1920) vw(50, 1920);
    border-radius: 10px;
    background: linear-gradient(90deg, #daf2f5 0%, #e9f5f5 55.29%, rgba(214, 240, 249, 0) 100%);
  }

  &-get-started-line {
    width: vw(16, 1920);
    height: 1px;
    background: var(--main-green);
  }

  &-list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: var(--g20);
    grid-row-gap: var(--g20);
  }

  &-item {
    border-radius: vw(10, 1920);
    padding: var(--p40);
    position: relative;
    height: vw(268, 1920);
    &--1 {
      grid-area: 1 / 1 / 2 / 5;
      background: var(--main-green);

      display: flex;
      flex-direction: column;
      gap: vw(50, 1920);

      .why-us-item-img {
        position: absolute;
        left: vw(40, 1920);
        top: 0;
        transform: translateY(-50%);
        width: vw(178, 1920);
        height: vw(160, 1920);
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    &--2 {
      grid-area: 1 / 5 / 2 / 9;
      background: var(--black);

      display: flex;
      flex-direction: column;
      gap: vw(50, 1920);

      .why-us-item-img {
        position: absolute;
        left: vw(40, 1920);
        top: 0;
        transform: translateY(-50%);
        width: vw(145, 1920);
        height: vw(148, 1920);
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    &--3 {
      grid-area: 1 / 9 / 2 / 13;
      background: var(--main-green);
      display: flex;
      flex-direction: column;
      gap: vw(26, 1920);
      padding: vw(40, 1920) vw(26, 1920) vw(40, 1920) vw(40, 1920);

      .why-us-item-list {
        display: flex;
        flex-direction: column;
        gap: vw(18, 1920);
      }

      .why-us-item-list-card {
        display: flex;
        align-items: center;
        gap: var(--g10);
        svg {
          width: vw(18, 1920);
          height: vw(18, 1920);
        }
        .text-black {
          line-height: 100%;
        }
      }
    }

    &--4 {
      grid-area: 2 / 1 / 3 / 13;
      overflow: hidden;
      display: flex;
      padding: 0;

      gap: vw(50, 1920);

      .why-us-item-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;

        .why-us-item-bg-img {
          position: relative;
          width: 100%;
          height: 100%;
          object-fit: cover;
          img {
            position: absolute;
            top: -20%;
            left: 0;
            width: auto;
            height: 140%;
            object-fit: cover;
            will-change: transform;
          }
        }

        .why-us-item-bg-svg {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: auto;
          img {
            width: 100%;
            height: auto;
            object-fit: cover;
          }
        }
      }
    }
  }

  &-item-header {
    display: flex;
    flex-direction: column;
    width: 100%;

    svg {
      width: vw(54, 1920);
      height: vw(28, 1920);
      align-self: flex-end;
    }
  }

  &-item-numbers {
    position: relative;
    z-index: 2;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
  }

  &-item-number-item {
    display: flex;
    flex-direction: column;
    width: 25%;
    height: 100%;
    padding: vw(85, 1920) vw(85, 1920);
  }
}
