@use '../assets/mixins' as *;
@use '../assets/vars' as *;
.benefits {
  &-inner {
    padding: var(--p80) var(--p260) vw(140, 1920) var(--p260);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--g40);
  }

  &-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--g20);
    width: vw(667, 1920);
  }

  &-list {
    display: flex;
    justify-content: space-between;
    gap: var(--g20);
  }

  &-item {
    &--1 {
      background: var(--yellow2);
      border-radius: 10px;
      width: vw(572, 1920);
      padding: vw(45, 1920) vw(38, 1920) vw(45, 1920) vw(158, 1920);
      position: relative;
    }

    &--2 {
      background: var(--main-green);
      border-radius: 10px;
      width: vw(808, 1920);
      padding: vw(50, 1920) vw(64, 1920) vw(50, 1920) vw(50, 1920);
      flex-grow: 1;
    }
  }

  &-item-img {
    position: absolute;

    width: vw(158, 1920);
    height: vw(214, 1920);
    bottom: 0;
    left: -6%;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &-item-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--g20);
  }

  &-item-list {
    display: flex;
    flex-direction: column;
    gap: var(--g16);
  }

  &-item-list-card {
    display: flex;
    align-items: center;
    gap: var(--g10);
    svg {
      width: vw(14, 1920);
      height: vw(14, 1920);
    }
    .text-black {
      line-height: 100%;
    }
  }
  &-item-text {
    display: flex;
    gap: vw(57, 1920);

    p {
      &:first-child {
        width: vw(366, 1920);
      }
      &:last-child {
        width: vw(271, 1920);
      }
    }
  }
}
