@use '../../assets/mixins' as *;
@use '../../assets/vars' as *;
.button {
  pointer-events: auto;
  outline: none;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  font: inherit;
  font-family: $default-font;
  line-height: normal;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1234px;
  gap: vw(16, 1920);

  @include mobile {
    gap: vw(16, 402);
  }
}

.button--l {
  height: vw(48, 1920);
  width: fit-content;
  padding: vw(13, 1920) vw(24, 1920);
}

.button--primary--green {
  background-color: var(--main-green);
  border: 1px solid var(--main-green);
  box-shadow: 0px 0px 25px 0px rgba(33, 155, 170, 0);
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: rgba(0, 0, 0, 0);
    box-shadow: 0px 0px 25px 0px rgba(33, 155, 170, 0.35);
    span {
      color: var(--main-green);
    }
  }

  &.disabled {
    background-color: #e2e2e2;
    box-shadow: none;
    span {
      color: var(--mid-grey);
    }
  }

  span {
    font-family: $default-font;
    font-size: vw(16, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: var(--white);
    transition: all 0.3s ease-in-out;

    @include mobile {
      font-size: vw(16, 402);
    }
  }
}

.button--primary--black {
  background-color: var(--black);
  border: 1px solid var(--black);
  box-shadow: 0px 0px 25px 0px rgba(33, 155, 170, 0);
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: rgba(0, 0, 0, 0);
    box-shadow: 0px 0px 25px 0px rgba(3, 8, 9, 0.35);
    span {
      color: var(--black);
    }
  }

  &.disabled {
    background-color: #e2e2e2;
    box-shadow: none;
    span {
      color: var(--mid-grey);
    }
  }

  span {
    font-family: $default-font;
    font-size: vw(16, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: var(--white);
    transition: all 0.3s ease-in-out;

    @include mobile {
      font-size: vw(16, 402);
    }
  }
}

.button--primary--white {
  background-color: var(--white);
  border: 1px solid var(--white);
  box-shadow: 0px 0px 25px 0px rgba(245, 245, 245, 0);
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: rgba(255, 255, 255, 0);
    box-shadow: 0px 0px 25px 0px rgba(245, 245, 245, 0.35);
    span {
      color: var(--main-green);
    }
  }

  span {
    font-family: $default-font;
    font-size: vw(16, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: var(--main-green);
    transition: all 0.3s ease-in-out;

    @include mobile {
      font-size: vw(16, 402);
    }
  }
}

.button--secondary--green {
  border: 1px solid var(--main-green);
  box-shadow: 0px 0px 25px 0px rgba(33, 155, 170, 0.25);
  background-color: rgba(0, 0, 0, 0);
  transition: all 0.3s ease-in-out;

  &:hover {
    box-shadow: 0px 0px 25px 0px rgba(33, 155, 170, 0);
    background: var(--main-green);

    span {
      color: var(--white);
    }
  }

  &.disabled {
    border: 1px solid #e2e2e2;
    box-shadow: none;
    span {
      color: #e2e2e2;
    }
    svg {
      fill: #e2e2e2;
    }
  }

  span {
    font-family: $default-font;
    font-size: vw(16, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: var(--main-green);
    transition: all 0.3s ease-in-out;
    @include mobile {
      font-size: vw(16, 402);
    }
  }
}

.button--secondary--black {
  border: 1px solid var(--basic-grey);
  background-color: transparent;
  transition: all 0.3s ease-in-out;

  &:hover {
    border: 1px solid var(--black);
  }

  &.disabled {
    border: 1px solid var(--basic-grey);
    box-shadow: none;
    span {
      color: var(--basic-grey);
    }
    svg {
      fill: var(--basic-grey);
    }
  }

  svg {
    fill: var(--black);
  }

  span {
    font-family: $default-font;
    font-size: vw(16, 1920);
    font-style: normal;
    letter-spacing: 0.224px;
    font-weight: 400;
    line-height: 150%;
    color: var(--black);
    @include mobile {
      font-size: vw(16, 402);
    }
  }
}
