@font-face {
  font-family: 'Nexa';
  src: url('../fonts/Nexa-Bold.eot');
  src: local('Nexa-Bold'), local('Nexa-Bold'),
    url('../fonts/Nexa-Bold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Nexa-Bold.woff2') format('woff2'), url('../fonts/Nexa-Bold.woff') format('woff'),
    url('../fonts/Nexa-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Nexa';
  src: url('../fonts/Nexa-Book.eot');
  src: local('Nexa Book'), local('Nexa-Book'),
    url('../fonts/Nexa-Book.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Nexa-Book.woff2') format('woff2'), url('../fonts/Nexa-Book.woff') format('woff'),
    url('../fonts/Nexa-Book.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
