@use '../assets/mixins' as *;
@use '../assets/vars' as *;
.features {
  padding: vw(146, 1920) var(--p260) vw(6, 1920) var(--p260);
  background: #1b1d1d;
  position: relative;

  &-bg {
    position: absolute;
    top: -2.5%;
    left: 30.5%;
    width: vw(1045, 1920);
    height: vw(1229, 1920);
    z-index: 1;

    svg {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }

  &-main {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: var(--g40);
    position: relative;
    padding-bottom: vw(42, 1920);

    &::before {
      content: '';
      position: absolute;
      bottom: vw(-60, 1920);
      left: vw(-20, 1920);
      width: 1px;
      height: vw(801, 1920);

      background: linear-gradient(
        90deg,
        rgba(153, 153, 153, 0) 0%,
        rgba(255, 255, 255, 0.1) 100%,
        rgba(153, 153, 153, 0) 0%
      );
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: vw(-260, 1920);
      width: 100vw;
      height: 1px;

      background: linear-gradient(-90deg, rgba(153, 153, 153, 0) 0%, rgba(255, 255, 255, 0.1) 100%);
    }
  }

  &-main-inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: vw(80, 1920);
  }

  &-main-swiper-pagination {
    width: vw(355, 1920);
    display: flex;
    flex-direction: column;

    position: relative;
  }

  &-main-swiper-pagination-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: vw(15, 1920);
    padding: vw(15, 1920) vw(20, 1920);
    background: rgba(255, 255, 255, 0);
    transition: all 0.3s ease-in-out;

    &.active {
      border-radius: 4px;
      background: linear-gradient(90deg, rgba(22, 61, 65, 0.6) 0%, rgba(32, 90, 97, 0) 100%);
      &::after {
        opacity: 1;
      }
      svg {
        stroke: var(--main-green);
      }
      span {
        color: var(--white);
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 2px;
      height: 100%;
      border-radius: 4px;
      background: #1e929f;
      opacity: 0;
      transition: all 0.3s ease-in-out;
    }

    svg {
      width: vw(20, 1920);
      height: vw(20, 1920);
      stroke: var(--grey3);
      transition: all 0.3s ease-in-out;
      pointer-events: none;
    }
  }

  &-main-swiper {
    width: vw(1103, 1920);
    height: vw(432, 1920);

    .swiper-wrapper {
      width: 100%;
      height: 100%;
    }
  }

  &-main-swiper-slide {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    gap: vw(73, 1920);
    align-items: flex-start;
    opacity: 0;

    &.swiper-slide-active {
      opacity: 1;
    }

    // background: linear-gradient(180deg, #111212 31.2%, rgba(17, 18, 18, 0) 76.04%);
  }

  &-main-swiper-slide-intro {
    width: vw(320, 1920);
    display: flex;
    flex-direction: column;
    gap: vw(20, 1920);

    .button--primary--green {
      margin-top: vw(20, 1920);
    }
  }

  &-main-swiper-slide-intro-descr {
    display: flex;
    flex-direction: column;
    gap: vw(20, 1920);
  }

  &-main-swiper-slide-img {
    border-radius: vw(8, 1920);
    border: vw(8, 1920) solid #30646b;
    background: linear-gradient(180deg, #ccdee0 0%, #80cbdb 100%);
    backdrop-filter: blur(17.54950523376465px);

    width: vw(709, 1920);
    height: vw(432, 1920);
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &-additional {
  }

  &-additional-title-wrap {
    padding: vw(40, 1920) vw(26, 1920) vw(35, 1920) vw(40, 1920);
    position: relative;
    width: fit-content;

    svg {
      position: absolute;
      top: 26%;
      right: 2%;

      width: vw(15, 1920);
      height: vw(14, 1920);
    }
  }

  &-additional-title {
    background: linear-gradient(340deg, #5f7477 0.03%, #333b3c 98.33%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &-additional {
    position: relative;
    z-index: 10;
  }

  &-additional-list-deco--1 {
    width: 50vw;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: flex-end;
    z-index: 11;
    svg {
      width: 100%;
      height: auto;
      stroke: #f5f7f9;
    }
  }

  &-additional-list-deco--2 {
    width: 50vw;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: flex-end;
    z-index: 11;
    svg {
      width: 100%;
      height: auto;
      stroke: #f5f7f9;
    }
  }

  &-additional-list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, vw(240, 1920));
    grid-column-gap: vw(20, 1920);
    grid-row-gap: vw(20, 1920);
  }

  &-additional-list-item {
    padding: vw(30, 1920);
    position: relative;
    border-radius: vw(4, 1920);
    background: var(--Dark-Black, #111212);
    overflow: hidden;
    &--1 {
      grid-area: 1 / 1 / 2 / 4;
      .features-additional-list-item-img {
        width: vw(240, 1920);
        height: vw(240, 1920);

        right: -25%;
        top: 0;
      }

      .features-additional-list-item-content-text {
        width: 60%;
      }
    }
    &--2 {
      grid-area: 1 / 4 / 3 / 7;
      .features-additional-list-item-img {
        width: 100%;
        height: auto;
        left: 0;
        bottom: 0;
      }
    }
    &--3 {
      grid-area: 1 / 7 / 2 / 11;
      .features-additional-list-item-img {
        width: vw(300, 1920);
        height: vw(240, 1920);
        top: 0;
        right: 0;
      }
      .features-additional-list-item-content-text {
        width: 50%;
      }
    }
    &--4 {
      grid-area: 1 / 11 / 2 / 13;
      .features-additional-list-item-img {
        width: vw(217, 1920);
        height: vw(100, 1920);
        bottom: -10%;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    &--5 {
      grid-area: 2 / 1 / 3 / 4;
      .features-additional-list-item-img {
        width: vw(240, 1920);
        height: vw(240, 1920);

        right: -25%;
        top: 0;
      }

      .features-additional-list-item-content-text {
        width: 60%;
      }
    }
    &--6 {
      grid-area: 2 / 7 / 3 / 10;
      .features-additional-list-item-img {
        width: vw(160, 1920);
        height: vw(240, 1920);

        right: 0;
        top: 0;
      }

      .features-additional-list-item-content-text {
        width: 60%;
      }
    }

    &--7 {
      grid-area: 2 / 10 / 3 / 13;
      .features-additional-list-item-img {
        width: vw(160, 1920);
        height: vw(240, 1920);

        right: 0;
        top: 0;
      }

      .features-additional-list-item-content-text {
        width: 60%;
      }
    }
    &--8 {
      grid-area: 3 / 1 / 4 / 3;
      .features-additional-list-item-img {
        width: vw(70, 1920);
        height: vw(88, 1920);
        top: 0;
        right: 0;
      }

      .features-additional-list-item-content-text {
        width: 90%;
      }
    }
    &--9 {
      grid-area: 3 / 3 / 4 / 7;
      .features-additional-list-item-img {
        width: vw(328, 1920);
        height: vw(240, 1920);

        right: -5%;
        top: 0;
      }

      .features-additional-list-item-content-text {
        width: 50%;
      }
    }
    &--10 {
      grid-area: 3 / 7 / 4 / 10;
      .features-additional-list-item-img {
        width: vw(240, 1920);
        height: vw(240, 1920);

        right: -25%;
        top: 0;
      }

      .features-additional-list-item-content-text {
        width: 60%;
      }
    }
    &--11 {
      grid-area: 3 / 10 / 4 / 13;
      .features-additional-list-item-img {
        width: vw(220, 1920);
        height: vw(240, 1920);
        right: -3%;
        top: 0;
      }
    }
    &--12 {
      grid-area: 4 / 1 / 6 / 13;
      align-items: center;
      padding: vw(40, 1920);
      .features-additional-list-item-img {
        width: vw(507, 1920);
        height: vw(258, 1920);
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
      }
      .features-additional-list-item-content {
        align-items: center;
        text-align: center;
      }
      .features-additional-list-item-content-text {
        width: 40%;
        text-align: center;
      }
    }
  }

  &-additional-list-item-img {
    position: absolute;

    z-index: 1;
    display: flex;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &-additional-list-item-content {
    display: flex;
    flex-direction: column;
    gap: vw(20, 1920);
    position: relative;
    z-index: 2;

    svg {
      width: vw(20, 1920);
      height: vw(20, 1920);
    }
  }

  &-additional-list-item-content-text {
    display: flex;
    flex-direction: column;
    gap: vw(10, 1920);
  }

  &-additional-list-item-deco {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    display: flex;

    img {
      position: absolute;

      will-change: transform, opacity;
    }

    &--1 {
      left: 3%;
      top: 5%;
      width: vw(31, 1920);
      height: vw(35, 1920);
    }

    &--2 {
      left: 14%;
      top: 20%;
      width: calc(26 / 1920 * 100vw);
      height: calc(26 / 1920 * 100vw);
    }
    &--3 {
      left: 23%;
      top: 40%;
      width: vw(64, 1920);
      height: vw(73, 1920);
    }
    &--4 {
      left: 30%;
      top: 13%;
      width: calc(31 / 1920 * 100vw);
      height: calc(35 / 1920 * 100vw);
    }
    &--5 {
      left: 60%;
      top: -3%;
      width: calc(45 / 1920 * 100vw);
      height: calc(45 / 1920 * 100vw);
    }
    &--6 {
      right: 22%;
      top: 40%;
      width: vw(64, 1920);
      height: vw(64, 1920);
    }
    &--7 {
      right: 15%;
      top: 32%;
      width: vw(54, 1920);
      height: vw(54, 1920);
    }
    &--8 {
      right: 3%;
      top: 7%;
      width: vw(31, 1920);
      height: vw(35, 1920);
    }
    &--9 {
      right: 30%;
      top: 50%;
      width: vw(80, 1920);
      height: vw(80, 1920);
    }
  }
}

.parent {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}
