@use '../assets/mixins' as *;
@use '../assets/vars' as *;

.hero {
  //   min-height: 150vh;
  &-inner {
    position: relative;
    width: 100%;
    height: vw(1000, 1920);
    min-height: vw(1000, 1920);
    overflow: hidden;
  }

  &-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  &-row-wrap {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
  }

  &-row {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: var(--g20);
    width: 100%;
    height: 100%;
  }

  &-row-item {
    position: relative;
    transition: all 0.3s ease;

    &--top-left {
      width: vw(1187, 1920);
      height: vw(486, 1920);

      svg {
        position: absolute;
        top: 0;
        left: 0;
        width: vw(1187, 1920);
        height: vw(901, 1920);
      }
    }

    &--top-right {
      //   width: vw(707, 1920);
      width: vw(713, 1920);
      height: vw(486, 1920);
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: vw(713, 1920);
        height: vw(901, 1920);
      }
    }

    &--bottom-left {
      width: vw(464, 1920);
      height: vw(494, 1920);

      svg {
        position: absolute;
        top: 0;
        right: 0;

        width: vw(1426, 1920);
        height: vw(1681, 1920);
      }
    }

    &--bottom-right {
      position: absolute;
      bottom: 0;
      right: vw(10, 1920);
      width: vw(1426, 1920);
      height: vw(504, 1920);
      z-index: 3;
      will-change: width, height, transform, top, left;

      svg {
        position: absolute;
        top: vw(10, 1920);
        left: 0;
        width: 100%;
        height: auto;
        z-index: 2;

        will-change: width, height, transform, top, left;
      }
    }
  }

  &-row-item-content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: vw(100, 1920) vw(70, 1920);
    gap: var(--g20);
  }

  &-row-item-content-title {
    text-align: center;
    width: 25%;
    background: linear-gradient(90deg, #5f7477 0.03%, #333b3c 98.33%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &-clip-animated-shape {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    aspect-ratio: cos(30deg);
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
    background: #1d2222;
    transition: all 0.3s ease;
    will-change: transform;
    transform: scale(1);
    z-index: 1;
  }

  &-content {
    position: absolute;
    z-index: 2;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: vw(124, 1920) var(--p260);
    gap: var(--g20);
    will-change: transform, opacity;
  }

  &-content-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: var(--g40);
    width: vw(405, 1920);
    height: vw(492, 1920);
  }

  &-content-left-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: var(--g30);

    h1 {
      width: 80%;
    }
    p {
      width: 80%;
    }
  }

  &-content-right {
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(983, 1920);
    height: vw(492, 1920);
    position: relative;
    gap: var(--g20);
  }

  &-swiper-pagination {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: var(--g10);
    width: vw(155, 1920);
  }

  &-swiper-pagination-item {
    border-radius: 69px;
    background: rgba(255, 255, 255, 0.2);

    backdrop-filter: blur(20px);
    width: fit-content;
    padding: vw(4, 1920) vw(12, 1920);

    &.active {
      background: var(--white);

      span {
        color: var(--main-green);
      }
    }

    span {
      white-space: nowrap;
    }
  }

  &-swiper-pagination-item--empty {
    border-radius: 69px;
    padding: vw(4, 1920) vw(12, 1920);

    border: 2px solid rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(20px);

    span {
      white-space: nowrap;
    }
  }

  &-swiper {
    width: vw(808, 1920);
    height: vw(492, 1920);
    position: relative;

    .swiper-wrapper {
      width: 100%;
      height: 100%;
    }

    .swiper-slide {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      width: vw(808, 1920);
      height: vw(492, 1920);
      overflow: hidden;
      border-radius: vw(8, 1920);

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      video {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
