@use './mixins' as *;
$default-font: 'Nexa', sans-serif;

$bezier: cubic-bezier(0.17, 0.67, 0, 1);

:root {
  --app-height: 100vh;
  --in: cubic-bezier(0.13, 0.81, 0.23, 0.96);
  --header-h: 7.29vw;
  --header-s: 5vw;
  --logo-w: 10.57vw;
  --logo-h: 4.68vw;
  --sliceFigure: 150px;
  --yellow: #f6ff00;
  --yellow2: #fac50e;
  --orange: #ff7c43;
  --white: #ffffff;
  --black: #1b1d1d;
  --black3: #1d2222;
  --devider: #e4eaf0;
  --grey900: #101828;
  --grey500: #667085;
  --grey200: #eaecf0;
  --black2: #242424;
  --main-green: #219baa;
  --grey2: #686b72;
  --grey3: #71767c;
  --grey5: #5d6169;
  --bg-grey: #f5f7f9;
  --line: linear-gradient(180deg, #485959 19.29%, rgba(72, 89, 89, 0) 74.51%);
  --line-color: #485959;
  --line-color-20: rgba(72, 89, 89, 0.2);
  --p260: calc((260 / 1920) * 100vw);
  --p190: calc((190 / 1920) * 100vw);
  --p160: calc((160 / 1920) * 100vw);
  --p120: calc((120 / 1920) * 100vw);
  --p100: calc((100 / 1920) * 100vw);
  --p90: calc((90 / 1920) * 100vw);
  --p80: calc((80 / 1920) * 100vw);
  --p70: calc((70 / 1920) * 100vw);
  --p60: calc((60 / 1920) * 100vw);
  --p50: calc((50 / 1920) * 100vw);
  --p40: calc((40 / 1920) * 100vw);
  --p30: calc((30 / 1920) * 100vw);
  --p20: calc((20 / 1920) * 100vw);
  --p10: calc((10 / 1920) * 100vw);
  --g100: calc((100 / 1920) * 100vw);
  --g90: calc((90 / 1920) * 100vw);
  --g80: calc((80 / 1920) * 100vw);
  --g60: calc((60 / 1920) * 100vw);
  --g40: calc((40 / 1920) * 100vw);
  --g30: calc((30 / 1920) * 100vw);
  --g24: calc((24 / 1920) * 100vw);
  --g20: calc((20 / 1920) * 100vw);
  --g18: calc((18 / 1920) * 100vw);
  --g16: calc((16 / 1920) * 100vw);
  --g12: calc((12 / 1920) * 100vw);
  --g10: calc((10 / 1920) * 100vw);
  --g8: calc((8 / 1920) * 100vw);
  --g5: calc((5 / 1920) * 100vw);
  --g4: calc((4 / 1920) * 100vw);
  --g2: calc((2 / 1920) * 100vw);
}

@media screen and (max-width: 1919px) {
  :root {
    --sliceFigure-big: 362px;
    --section-v-offset: 100px;
    --p375: 9.5vw;
  }
}

@media screen and (max-width: 1023px) {
  :root {
    --logo-w: 19.8vw;

    --header-s: 112px;
    --sliceFigure-big: 44px;
    --unit: 6.25vw;
    --header-h: 12.5vw;

    --p100: vw(100, 768);
    --p80: vw(80, 768);
    --p60: vw(60, 768);
    --p50: vw(50, 768);
    --p40: vw(40, 768);
    --p30: vw(30, 768);
    --p20: vw(20, 768);
    --p10: vw(10, 768);
    --g40: vw(40, 768);
    --g30: vw(30, 768);
    --g20: vw(20, 768);
    --g10: vw(10, 768);
    --g5: vw(5, 768);
    --g2: vw(2, 768);
  }
}
@media screen and (max-width: 767px) {
  :root {
    --sliceFigure: 100px;
    --sliceFigure-big: 100px;

    --logo-w: 27.5vw;
    --p100: vw(100, 402);
    --p80: vw(80, 402);
    --p60: vw(60, 402);
    --p50: vw(50, 402);
    --p40: vw(40, 402);
    --p30: vw(30, 402);
    --p20: vw(20, 402);
    --p10: vw(10, 402);
    --g40: vw(40, 402);
    --g30: vw(30, 402);
    --g20: vw(20, 402);
    --g10: vw(10, 402);
    --g5: vw(5, 402);
    --g2: vw(2, 402);
  }
}

