@use './assets/mixins' as *;
@use './assets/vars' as *;
.footer {
  position: relative;
  z-index: 2;
  background: var(--black);
  overflow: hidden;
  display: flex;
  flex-direction: column;

  &-top {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 var(--p190);

    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      opacity: 0.2;
      background: var(--line-color);
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      opacity: 0.2;
      background: var(--line-color);
    }
  }

  &-top-deco {
    display: flex;
    align-items: center;
    justify-content: center;
    height: vw(79, 1920);
    width: 100%;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1px;
      height: 100%;
      opacity: 0.2;
      background: var(--line-color);
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      opacity: 0.2;
      background: var(--line-color);
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &-deco-left {
    width: var(--p190);
    height: 100%;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      opacity: 0.2;
      background: var(--line-color);
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &-deco-right {
    width: var(--p190);
    height: 100%;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1px;
      height: 100%;
      opacity: 0.2;
      background: var(--line-color);
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &-inner {
    display: flex;
  }

  &-content-wrap {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &-content {
    display: flex;
    padding: var(--p90) var(--p70) var(--p70) var(--p70);
    position: relative;
    gap: vw(169, 1920);

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 100vw;
      height: 1px;
      opacity: 0.2;
      background: var(--line-color);
    }
  }

  &-intro {
    display: flex;
    flex-direction: column;
    width: vw(231, 1920);
  }

  &-logo {
    margin-bottom: var(--p30);
    display: flex;
    align-items: center;
    svg {
      width: vw(133.339, 1920);
      height: vw(33.975, 1920);
    }
  }

  &-text {
    margin-bottom: var(--p60);
  }

  &-socials {
    display: flex;
    flex-wrap: wrap;
  }

  &-socials-item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(60, 1920);
    height: vw(60, 1920);
    padding: var(--p20);
    border-right: 1px solid var(--line-color-20);
    border-top: 1px solid var(--line-color-20);
    transition: all 0.3s ease-in-out;

    &:first-child {
      border-left: 1px solid var(--line-color-20);
    }

    &:nth-child(4n) {
      border-left: 1px solid var(--line-color-20);
    }

    &:nth-last-child(-n + 3) {
      border-bottom: 1px solid var(--line-color-20);
    }

    &:hover {
      background: var(--main-green);
      color: var(--white);

      svg {
        fill: var(--white);
      }
    }

    svg {
      width: 100%;
      height: 100%;
      fill: var(--grey5);
      transition: all 0.3s ease-in-out;
    }
  }

  &-links-group {
    display: flex;
    gap: var(--g100);
  }

  &-links-wrap {
    display: flex;
    flex-direction: column;
    gap: var(--g40);
  }

  &-links {
    display: flex;
    flex-direction: column;
    gap: var(--g18);

    a {
      text-decoration: none;
      display: flex;
      position: relative;
      width: fit-content;

      &:hover {
        &::after {
          width: 100%;
        }
      }

      &::after {
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 0;
        height: 1px;

        background: var(--white);
        transition: width 0.3s ease-in-out;
      }
    }
  }

  &-content-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--p30) var(--p70);
    position: relative;
    gap: vw(169, 1920);

    * {
      font-weight: 300;
    }
  }

  &-content-bottom-privacy {
    display: flex;
    align-items: center;
    gap: var(--g10);

    a {
      text-decoration: none;
      transition: color 0.3s ease-in-out;

      &:hover {
        color: var(--white);
      }
    }
  }
}
