@use '../assets/mixins' as *;
@use '../assets/vars' as *;

.about {
  position: relative;
  width: 100%;

  //   transform: translateY(-50%);

  &-inner {
    background: var(--black3);
    // position: absolute;
    // width: 100%;
    // height: 100vh;
    // overflow: hidden;
    // transform: translate(0, -50%);
    z-index: 1;
  }

  &-list-wrap {
    padding: vw(60, 1920) vw(210, 1920) vw(104, 1920) vw(210, 1920);
  }

  &-list {
    position: relative;
    z-index: 2;
  }

  &-list-inner {
    display: flex;
    flex-wrap: wrap;
    padding: vw(50, 1920) vw(168, 1920) 0 vw(168, 1920);
  }

  &-list-button {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
    z-index: 11;
  }

  &-list-deco {
    &--1 {
      z-index: 1;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: 11;
      width: 100vw;
      height: 1px;
      opacity: 0.2;
      background: linear-gradient(180deg, #485959 60.3%, rgba(72, 89, 89, 0) 100%);
    }

    &--2 {
      z-index: 1;
      position: absolute;
      top: vw(50, 1920);
      left: 50%;
      transform: translateX(-50%);
      z-index: 11;
      width: 100vw;
      height: 1px;
      opacity: 0.2;
      background: linear-gradient(180deg, #485959 60.3%, rgba(72, 89, 89, 0) 100%);
    }

    &--3 {
      z-index: 1;
      position: absolute;
      top: vw(350, 1920);
      left: 50%;
      transform: translateX(-50%);
      z-index: 11;
      width: 100vw;
      height: 1px;
      opacity: 0.2;
      background: linear-gradient(180deg, #485959 60.3%, rgba(72, 89, 89, 0) 100%);
    }

    &--4 {
      z-index: 1;
      position: absolute;
      top: vw(650, 1920);
      left: 50%;
      transform: translateX(-50%);
      z-index: 11;
      width: 100vw;
      height: 1px;
      opacity: 0.2;
      background: linear-gradient(180deg, #485959 60.3%, rgba(72, 89, 89, 0) 100%);
    }

    &--5 {
      z-index: 1;
      position: absolute;
      top: vw(50, 1920);
      left: 50%;
      transform: translateX(-50%);
      z-index: 11;
      width: 100vw;
      height: 1px;
      opacity: 0.2;
      background: linear-gradient(180deg, #485959 60.3%, rgba(72, 89, 89, 0) 100%);
    }

    &--frame {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      svg {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
    }
  }

  &-item {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: vw(24, 1920);
    padding: vw(60, 1920) vw(52, 1920);
    padding: vw(60, 1920) vw(52, 1920) vw(72, 1920) vw(52, 1920);

    // &:nth-child(1) {
    //   padding: vw(60, 1920) vw(52, 1920) vw(72, 1920) vw(52, 1920);
    // }
    &:nth-child(-n + 4) {
      padding: vw(57, 1920) vw(52, 1920) vw(83, 1920) vw(52, 1920);
    }
    // &:nth-child(n + 4) {
    //   padding: vw(60, 1920) vw(52, 1920) vw(72, 1920) vw(52, 1920);
    // }
  }

  &-item-icon {
    width: vw(40, 1920);
    height: vw(40, 1920);
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &-item-content {
    text-align: center;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: vw(10, 1920);

    .text-12-r {
      line-height: 110%;
    }
  }

  &-content {
    position: relative;
    z-index: 2;
    padding: vw(102, 1920) 0 0 vw(168, 1920);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: vw(60, 1920);
  }

  &-content-top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    gap: vw(20, 1920);
    position: relative;
    padding-right: vw(108, 1920);
  }

  &-content-intro {
    display: flex;
    flex-direction: column;
    gap: vw(30, 1920);
    width: vw(454, 1920);

    p {
      width: 90%;
    }
  }

  &-content-owner-img {
    width: vw(454, 1920);
    height: vw(383, 1920);
    position: absolute;
    bottom: vw(-60, 1920);
    left: 45%;
    transform: translateX(-50%);
    z-index: 1;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &-content-owner-info {
    width: vw(400, 1920);
  }

  &-content-owner-info__name {
    margin-top: vw(13, 1920);
    margin-bottom: vw(10, 1920);
  }

  &-content-owner-info__description {
    margin-top: vw(26, 1920);
  }

  &-content-bottom {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &-content__map-wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    padding: var(--p40);
    width: vw(335, 1920);
    height: vw(306, 1920);
    gap: vw(35, 1920);
    overflow: hidden;
    border-radius: vw(10, 1920);

    svg {
      position: relative;
      z-index: 1;
      width: vw(26, 1920);
      height: vw(31, 1920);
    }
    span {
      position: relative;
      z-index: 2;
    }
  }

  &-content__map {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    z-index: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &-content-bottom__text-wrap {
    width: vw(976, 1920);
    height: vw(306, 1920);
    overflow: hidden;
    border-radius: vw(10, 1920);
    background: var(--main-green);
    padding: vw(51, 1920) vw(142, 1920) vw(57, 1920) vw(50, 1920);
    display: flex;
    flex-direction: column;
    gap: vw(30, 1920);
  }

  &-content-bottom__text-header {
    display: flex;
    align-items: center;
    gap: vw(35, 1920);

    span {
      line-height: 120%;
      width: 60%;
    }
  }

  &-content-bottom__text-header-logo {
    width: vw(172, 1920);
    height: vw(44, 1920);
  }

  &-content-bottom__text-header-arrows {
    width: vw(49, 1920);
    height: vw(24, 1920);
  }

  &-content-bottom__text {
    width: vw(579, 1920);
  }

  &-shape--1 {
    position: absolute;
    bottom: 0;
    left: 0;

    width: 50vw;
    display: flex;
    align-items: flex-end;
    svg {
      width: 100%;
      height: auto;
      object-fit: cover;
      stroke: #f5f7f9;
    }
  }
  &-shape--2 {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 3;

    width: 50vw;
    display: flex;
    align-items: flex-end;
    svg {
      width: 100%;
      height: auto;
      object-fit: cover;
      stroke: #f5f7f9;
    }
  }

  &-arrow {
    position: absolute;
    bottom: vw(29, 1920);
    left: 50%;
    transform: translateX(-50%);

    width: vw(16, 1920);
    height: vw(89, 1920);

    svg {
      width: 100%;
      height: 100%;
    }
  }
}

.companies {
  padding-top: vw(20, 1920);
  position: relative;
  background: var(--white);
  background: linear-gradient(to right, #f5f7f9 50%, #fff 50%);

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: vw(20, 1920);
    height: vw(20, 1920);

    clip-path: polygon(0 0, 0 100%, 100% 0);
    background: #f5f7f9;
  }
  &-homepage {
    position: relative;
    width: 100%;
    height: vw(63, 1920);

    overflow: hidden;
    display: flex;
    align-items: center;

    @include mobile {
      height: vw(27, 402);
    }

    &-wrap {
      padding: vw(40, 1920) 0;
      border-bottom: 1px solid #e4eaf0;
      border-top: 1px solid #e4eaf0;
    }

    &-title-wrap {
      position: relative;
      z-index: 2;
      padding: 0 var(--p260);
      //   border-top: 1px solid #e4eaf0;

      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: vw(210, 1920);
        transform: translate(-100%);
        width: 1px;
        height: vw(543, 1920);

        background: #e4eaf0;
        z-index: 4;
      }

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;

        width: 50vw;
        height: 1px;

        background: #e4eaf0;
        z-index: 4;
      }
    }

    &-title {
      display: flex;
      justify-content: flex-start;
    }

    &-title-button {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: vw(7, 1920);
      padding: vw(30, 1920) vw(44, 1920);
      border-right: 1px solid #e4eaf0;
      text-decoration: none;
      transition: all 0.3s ease;

      &:first-child {
        pointer-events: none;
        text-decoration: none;
        border-left: 1px solid #e4eaf0;
      }

      &:hover {
        background: var(--main-green);
        color: var(--white);
        span {
          color: var(--white);
        }
        svg {
          fill: var(--white);
          stroke: var(--white);
        }
      }
      span {
        transition: all 0.3s ease;
      }

      svg {
        width: vw(11, 1920);
        height: vw(10, 1920);
        fill: var(--main-green);
        stroke: var(--main-green);
        transition: all 0.3s ease;
      }
    }

    &-list {
      position: absolute;
      height: 100%;
      width: 200%;
      display: flex;
      align-items: center;
      gap: vw(100, 1920);

      @include mobile {
        gap: vw(32, 402);
      }
    }

    &-item {
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        height: vw(63, 1920);
        width: auto;
        max-width: fit-content;

        @include mobile {
          height: vw(27, 402);
        }
      }
    }
  }
}
