@use './assets/mixins' as *;
@use './assets/vars' as *;
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;

  z-index: 40;
  transition: all 1500ms cubic-bezier(0.06, 0.65, 0.2, 1);
  transform: translateY(0%);

  &.header-hidden {
    transform: translateY(-100%);
  }

  // &.top {
  //   background: transparent;
  // }

  &.bg {
    background: var(--black3);

    .header-inner {
      margin-top: 0;
    }
  }

  &-inner {
    margin-top: vw(25, 1920);
    position: relative;
    z-index: 10;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--p20) var(--p260);
    transition: all 0.3s ease-in-out;
  }

  %-logo {
    position: relative;
    width: vw(133, 1920);
    height: vw(34, 1920);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;

    svg {
      width: 100%;
      height: auto;
      transition: all 0.3s ease-in-out;
    }
  }

  &-left {
    display: flex;
    align-items: center;
    gap: vw(148, 1920);
  }

  &-links {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g40);
  }

  &-link {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    text-decoration: none;

    a {
      text-decoration: none;
      display: flex;

      &::after {
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 0;
        height: 1px;
        background: var(--white);
        transition: width 0.3s ease-in-out;
      }

      &:hover {
        &::after {
          width: 100%;
        }
      }
    }
  }
}
