@use '../assets/mixins' as *;
@use '../assets/vars' as *;
.faq {
  position: relative;
  width: 100%;
  height: vw(1642, 1920);
  border-top: 1px solid #e8eaee;
  background: var(--bg-grey);

  &-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
  &-bg-deco {
    position: absolute;

    z-index: -1;

    // aspect-ratio: 1/1;
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
    overflow: hidden;

    &--1 {
      width: vw(390, 1920);
      height: vw(460, 1920);
      bottom: vw(1171, 1920);
      left: vw(10, 1920);
    }

    &--2 {
      transform: translateX(-50%);
      width: vw(954, 1920);
      height: vw(1123, 1920);
      bottom: vw(155, 1920);
      left: 0;
    }
    &--3 {
      width: vw(390, 1920);
      height: vw(460, 1920);
      bottom: vw(1171, 1920);
      right: vw(10, 1920);
    }

    &--4 {
      transform: translateX(50%);
      width: vw(954, 1920);
      height: vw(1123, 1920);
      bottom: vw(155, 1920);
      right: 0;
    }
  }

  &-bg-deco-img {
    position: relative;
    transition: box-shadow 0.3s;
    will-change: transform;
    display: inline-block;
    img {
      display: block;
      width: 110%;
      height: 110%;
      object-fit: cover;
      pointer-events: none;
      user-select: none;
    }
  }

  &-content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    padding-top: vw(100, 1920);
  }

  &-intro {
    position: relative;
    width: 100%;
    height: vw(704, 1920);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding: vw(100, 1920) vw(70, 1920) vw(155, 1920) vw(70, 1920);
    gap: vw(50, 1920);
  }

  &-intro-bg-wrap {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    svg {
      width: 100%;
      height: auto;
      stroke: var(--black);
      object-fit: cover;
    }
  }

  &-intro-text {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: vw(30, 1920);
  }

  &-intro-btns {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g20);
  }

  &-list-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: vw(60, 1920);
    width: vw(727, 1920);
  }

  &-item {
    padding: vw(24, 1920) 0 vw(32, 1920) 0;
    border-bottom: 1px solid var(--grey200);
    display: flex;
    flex-direction: column;

    &:last-child {
      border-bottom: none;
    }

    &.active {
      .faq-item-header {
        .faq-item-header-plus--v {
          opacity: 0;
        }
      }

      .faq-item-text {
        max-height: 400px; // Arbitrary large value to allow expansion
        overflow: visible;
        margin-top: vw(8, 1920);
        opacity: 1;
        transition: all 0.3s ease-in-out;
      }
    }
  }

  &-item-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    width: 100%;
    gap: vw(20, 1920);
  }

  &-item-header-plus-wrap {
    position: relative;
    width: vw(24, 1920);
    height: vw(24, 1920);
  }

  &-item-header-plus--h {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: vw(12, 1920);
    height: vw(2, 1920);
    background-color: var(--main-green);
    border-radius: 10px;
  }

  &-item-header-plus--v {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: vw(2, 1920);
    height: vw(12, 1920);
    border-radius: 10px;
    background-color: var(--main-green);
    transition: all 0.3s ease-in-out;
    opacity: 1;
  }

  &-item-text {
    margin-top: 0;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }

  &-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: vw(20, 1920);
    text-align: center;
  }
}
